.body{
    width:100%;
    background-color: rgba(245, 244, 238, 0.719);
}
.goback{
    margin-top: 15px;
    margin-left: 6px;
    width: 35px;
    height: 30px;
}
.gouwu{
    margin-top: 12px;
    margin-right: 10px;
    width: 35px;
    height: 35px;
    float: right;
}
.bottom{
    width: 100%;
    height: 48px;
    position: fixed;
    bottom: 0;
    background-color: white;
}
.icon-1{
    width: 45px;
    height: 100%;
    float: left;
    position: relative;
}
.icon-1 img{
    width: 50%;
    height: 20px;
    top: 6px;
    left: 7px;
    float: left;
    position: relative;
}
.icon span{
    margin-top: 3px;
    margin-left: 7px;
    font-size: 12px;
    float: left;
}

.shop{
    width: 240px;
    height: 48px;
    left:0;
    float: left;
    position: relative;
}
.right ,.left{
    margin-top: 5px;
    width: 115px;
    height: 38px;

    float: left;
    position: relative;
}
.left{
    background-color: rgb(236, 158, 14);
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}
.right{
    background-color: rgb(246, 107, 12);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}
.left span , .right span{
    top: 9px;
    left: 25px;
    color: white;
    font-size: 14px;
    font-weight: bold;
    position: relative;
}